body {
    background-image: url(./assets/img/bg.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: repeat-y;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100dvh;
    font-family: 'Poppins', sans-serif;
}

#root {
    width: 100%;
    height: 100dvh;
}

.overlay {
    background: rgba(0, 0, 0, 0.1); /* بهتر دیده بشه */
    display: flex;
    justify-content: center;
    align-items: center;
}

.app {
    text-align: center;
    color: white;
    max-width: 900px;
    width: 100%;
}

.app h2 {
    font-size: 32px;
    margin-bottom: 40px;
    font-weight: 600;
    color: whitesmoke;
}

.gift-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
